import React from "react";
import { connect } from "react-redux";
import { Helmet } from 'react-helmet';
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap";
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb";
import BettingInspectionTable from './Tables/BettingInspectionTable';
import BettingInspectionModal from './Modals/BettingInspectionModal';
import SystemStatusTypeSearch from '../../SearchComponents/SystemStatusTypeSearch/SystemStatusTypeSearch';
import * as API from "../../../../utility/crud/app.crud";
import * as Date from "../../../../utility/dateAndTime";
import { toastr } from 'react-redux-toastr';
import BusinessPopover from '../../EyeComponents/BusinessPopover/BusinessPopover'
import ClientPopover from '../../EyeComponents/ClientPopover/ClientPopover'

class BettingInspection extends React.Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
            validated: false,
            errors: {},
            columns: [],
            data: [],
            dataPopover: [],
            filteredData: [],
            filteredBusinessData: [],
            filteredStatusData: [],
            value: '',
            show: false,
            showdelete: false,
            REG_BETTING_ID: 0,
            REG_BETTING_INSPECTION_ID: 0,
            REG_BETTING_NUMBER: 0,
            INSPECTION_DETAILS: '',
            REG_BUSINESS_ID: 0,
            SYS_STATUS_ID: 0,
            BUSINESSID: 0,
            CLIENTID: 0,
            BUSINESS_ID: 0,
            BUSINESS_TYPE_NAME: '',
            DOCUMENT_TYPE_NAME: '',
            CREATEDBY: props.user.EMAIL,
            MODIFIEDBY: props.user.EMAIL,
            CREATED_DATE: Date.ShowDate(),
            MODIFIED_DATE: Date.ShowDate(),
            CLIENT_DOCUMENT_NUMBER: '',
            CLIENT_POSTAL_ADDRESS: '',
            CLIENT_MOBILE_NUMBER: '',
            BETTING_NUMBER: 0,
            InsertUpdate: '',
            openbusinesssearchEye: false,
            openclientsearchEye: false,
            openstatussearch: false,
            BUSINESS_NAME: '',
            SYSTEMSTATUSTYPEDESCRIPTION: '',
            searchcolumns: [],
            searchdata: [],
            searchstatusdata: [],
            searchFilteredStatusData: [],
            searchstatusvalue: '',



        }

        this.handleShow = () => {
            this.setState({
                show: true,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }


        this.handleShowClientSearchEye = () => {
            this.setState({ openclientsearchEye: true });
        };
        this.handleShowBusinessSearchEye = () => {
            this.setState({ openbusinesssearchEye: true });
        };
        this.handleShowStatusSearch = () => {
            this.setState({ openstatussearch: true });
        };
        this.handleHideBusinessSearchEye = () => {
            this.setState({ openbusinesssearchEye: false });
        };
        this.handleHideClientSearchEye = () => {
            this.setState({ openclientsearchEye: false });
        };
        this.handleHideStatusSearch = () => {
            this.setState({ openstatussearch: false });
        };


        this.populateData();
        this.populateStatus();

    }

    populateData() {
        const getdata = `
            query {
                AllBettingInspection{
                    REG_BETTING_ID
                    BETTING_NUMBER
                    REG_BETTING_INSPECTION_ID
                    REG_BETTING_NUMBER
                    INSPECTION_DETAILS
                    SYS_STATUS_ID
                    SYS_STATUS_NAME
                    REG_BUSINESS_ID
                    BUSINESS_REGISTER {
                        BUSINESS_ID
                        BUSINESS_NAME
                        BUSINESSTYPE {
                            BUSINESS_TYPE_ID
                            BUSINESS_TYPE_NAME
                        }
                        BUSINESS_DOCUMENT_TYPE {
                            DOCUMENT_TYPE_NAME
                        }
                        CLIENTID {
                            CLIENT_ID
                            CLIENT_NAME
                            CLIENT_TYPE_ID
                            CLIENT_DOCUMENT_NUMBER
                            POSTAL_ADDRESS
                            MOBILE_NUMBER 
                        }
                        SUB_COUNTY {
                            SUBCOUNTY_NAME
                        }
                        WARD {
                            WARD_NAME
                        }
                    }
                    
                }

            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                data: data.data.AllBettingInspection
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load  Betting Inspection Data');
                }
            })


    }




    populateStatus() {
        const getdata = `
            query {
                SystemStatusFilter (
                    CATEGORY:"INSPECTION"
                    ){
                        SYS_STATUS_ID,
                        SYS_STATUS_NAME
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                searchstatusdata: data.data.SystemStatusFilter
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to System Status Type Load Data');
                }
            })
    }

    handleSubmit = (event) => {
        event.preventDefault();

        const form = event.currentTarget;
        if (form.checkValidity() === false) {
            event.preventDefault();
            event.stopPropagation();
        } else {

            const formAdd = `
        mutation{
            addBettingInspection( 
                REG_BETTING_NUMBER :${this.state.REG_BETTING_ID},              
                BUSINESS_ID:${this.state.BUSINESS_ID},
                CLIENT_ID:${this.state.CLIENT_ID},                                 
                SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                INSPECTION_DETAILS:"${this.state.INSPECTION_DETAILS}",
                CREATEDBY:"${this.state.CREATEDBY}",
                MODIFIEDBY:"${this.state.MODIFIEDBY}",
                CREATED_DATE:"${this.state.CREATED_DATE}",
                MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                ){
                    REG_BETTING_INSPECTION_ID
            }
        }
        `;

            const formEdit = `
        mutation{
            updateBettingInspection(
                REG_BETTING_INSPECTION_ID:${this.state.REG_BETTING_INSPECTION_ID},              
                BUSINESS_ID:${this.state.BUSINESS_ID},
                SYS_STATUS_ID:${this.state.SYS_STATUS_ID},
                INSPECTION_DETAILS:"${this.state.INSPECTION_DETAILS}",
                CREATEDBY:"${this.state.CREATEDBY}",
                MODIFIEDBY:"${this.state.MODIFIEDBY}",
                CREATED_DATE:"${this.state.CREATED_DATE}",
                MODIFIED_DATE:"${this.state.MODIFIED_DATE}"
                ){
                    REG_BETTING_INSPECTION_ID
                }
            }
            `;
            // CLIENT_ID:${this.state.CLIENT_ID},
            // REG_BETTING_NUMBER :${this.state.REG_BETTING_ID}, 

            if (this.state.InsertUpdate === 'Insert') {
                API.create(formAdd)
                    .then(() => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.success('Create Record', 'Record Created Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Create Data');
                        }
                    })
            } else {
                API.update(formEdit)
                    .then((resp) => {
                        this.clearData();
                        this.handleHide();
                        this.populateData();
                        toastr.info('Update Record', 'Record Updated Successfully');
                    }).catch(error => {
                        if (error.response || error.request) {
                            toastr.error('Error Loading Data', 'Failed to Update Data');
                        }
                    })
            }



        }

        this.setState({
            validated: true
        })


    }

    handleDelete = (event) => {


        event.preventDefault();
        const formDelete = `
        mutation{
            deleteBettingInspection(
                REG_BETTING_INSPECTION_ID:${this.state.REG_BETTING_INSPECTION_ID}
                ){
                    REG_BETTING_INSPECTION_ID
            }
        }
        `;

        API.remove(formDelete)
            .then(() => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })



    }




    handleCloseStatusSearch = (e) => {

        this.setState({
            SYS_STATUS_ID: e.SYS_STATUS_ID,
            SYSTEMSTATUSTYPEDESCRIPTION: e.SYS_STATUS_NAME,
            openstatussearch: false,

        });

    };

    handleEditData = (e) => {


        if (e.REG_BETTING_INSPECTION_ID !== null) {
            this.setState({
                SYS_STATUS_ID: e.SYS_STATUS_ID,
                SYSTEMSTATUSTYPEDESCRIPTION: e.SYS_STATUS_NAME,
                INSPECTION_DETAILS: e.INSPECTION_DETAILS,
                REG_BETTING_ID: e.REG_BETTING_ID,
                REG_BETTING_INSPECTION_ID: e.REG_BETTING_INSPECTION_ID,
                BUSINESS_NAME: e.BUSINESS_REGISTER.BUSINESS_NAME,
                BUSINESS_ID: e.BUSINESS_REGISTER.BUSINESS_ID,
                CLIENT_ID: e.BUSINESS_REGISTER.CLIENTID.CLIENT_ID,
                REG_BETTING_NUMBER: e.REG_BETTING_NUMBER,
                BUSINESS_TYPE_NAME: e.BUSINESS_REGISTER.BUSINESSTYPE.BUSINESS_TYPE_NAME,
                DOCUMENT_TYPE_NAME: e.BUSINESS_REGISTER.BUSINESS_DOCUMENT_TYPE.DOCUMENT_TYPE_NAME,
                CLIENT_NAME: e.BUSINESS_REGISTER.CLIENTID.CLIENT_NAME,
                CLIENT_DOCUMENT_NUMBER: e.BUSINESS_REGISTER.CLIENTID.CLIENT_DOCUMENT_NUMBER,
                CLIENT_POSTAL_ADDRESS: e.BUSINESS_REGISTER.CLIENTID.POSTAL_ADDRESS,
                CLIENT_MOBILE_NUMBER: e.BUSINESS_REGISTER.CLIENTID.MOBILE_NUMBER,
                REG_BUSINESS_ID: e.REG_BUSINESS_ID,
                BETTING_NUMBER: e.BETTING_NUMBER,
                SUBCOUNTY_NAME: e.BUSINESS_REGISTER.SUB_COUNTY.SUBCOUNTY_NAME,
                show: true,

                validated: false,
                InsertUpdate: 'Update',
            })
        }
        else {
            this.setState({
                SYS_STATUS_ID: '',
                SYSTEMSTATUSTYPEDESCRIPTION: '',
                INSPECTION_DETAILS: '',
                REG_BETTING_ID: e.REG_BETTING_ID,
                REG_BETTING_INSPECTION_ID: e.REG_BETTING_INSPECTION_ID,
                BUSINESS_NAME: e.BUSINESS_REGISTER.BUSINESS_NAME,
                BUSINESS_ID: e.BUSINESS_REGISTER.BUSINESS_ID,
                CLIENT_ID: e.BUSINESS_REGISTER.CLIENTID.CLIENT_ID,
                REG_BETTING_NUMBER: e.REG_BETTING_NUMBER,
                BUSINESS_TYPE_NAME: e.BUSINESS_REGISTER.BUSINESSTYPE.BUSINESS_TYPE_NAME,
                DOCUMENT_TYPE_NAME: e.BUSINESS_REGISTER.BUSINESS_DOCUMENT_TYPE.DOCUMENT_TYPE_NAME,
                CLIENT_NAME: e.BUSINESS_REGISTER.CLIENTID.CLIENT_NAME,
                CLIENT_DOCUMENT_NUMBER: e.BUSINESS_REGISTER.CLIENTID.CLIENT_DOCUMENT_NUMBER,
                CLIENT_POSTAL_ADDRESS: e.BUSINESS_REGISTER.CLIENTID.POSTAL_ADDRESS,
                CLIENT_MOBILE_NUMBER: e.BUSINESS_REGISTER.CLIENTID.MOBILE_NUMBER,
                REG_BUSINESS_ID: e.REG_BUSINESS_ID,
                BETTING_NUMBER: e.BETTING_NUMBER,
                SUBCOUNTY_NAME: e.BUSINESS_REGISTER.SUB_COUNTY.SUBCOUNTY_NAME,
                show: true,
                InsertUpdate: 'Insert',
                validated: false,
            })
        }

    }

    handleDeleteData = (e) => {
        if (e.REG_BETTING_INSPECTION_ID > 0) {
            this.setState({
                REG_BETTING_INSPECTION_ID: e.REG_BETTING_INSPECTION_ID,
                BUSINESS_NAME: e.BUSINESS_REGISTER.BUSINESS_NAME,
                showdelete: true,
                InsertUpdate: 'Delete'
            })
        }
        else {
            this.setState({
                showdelete: false
            })
            toastr.warning('Delete Record', 'You not allowed to delete Awaiting Record');
        }
    }

    clearData() {
        this.setState({
            BUSINESS_NAME: '',
            CLIENT_NAME: '',
            REG_BUSINESS_ID: '',
            INSPECTION_DETAILS: '',
            SYS_STATUS_ID: '',
            SYSTEMSTATUSTYPEDESCRIPTION: '',
            validated: false,
        })
    }

    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.BUSINESS_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.INSPECTION_DETAILS.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.SYSTEMSTATUS.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.BUSINESS_NAME.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.INSPECTION_DETAILS.toString().toLowerCase().startsWith(value.toLowerCase()) ||
                    item.SYSTEMSTATUS.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    handleSearchBusinessFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchbusinessdata
        let filteredBusinessData = this.state.searchFilteredBusinessData
        this.setState({ searchbusinessvalue: value });

        if (value.length) {
            filteredBusinessData = data.filter(item => {
                let startsWithCondition =
                    item.BUSINESS_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.BUSINESS_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredBusinessData })
        }
    }

    handleSearchStatusFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchstatusdata
        let filteredStatusData = this.state.searchFilteredStatusData
        this.setState({ searchstatusvalue: value });

        if (value.length) {
            filteredStatusData = data.filter(item => {
                let startsWithCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.SYS_STATUS_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredStatusData })
        }
    }
    handleSearchClientRegistrationFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchclientregistrationdata
        let filteredClientRegistrationData = this.state.searchFilteredClientRegistrationData
        this.setState({ searchclientregistrationvalue: value });

        if (value.length) {
            filteredClientRegistrationData = data.filter(item => {
                let startsWithCondition =
                    item.CLIENT_NAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.CLIENT_NAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredClientRegistrationData })
        }
    }

    render() {

        const { data, columns, value, filteredData, filteredStatusData, searchstatusvalue, searchstatusdata } = this.state

        return (
            <>
                <Helmet><title>Betting Inspection</title></Helmet>

                <React.Fragment>
                    <Breadcrumbs
                        breadCrumbTitle="Registers"
                        breadCrumbParent="Business"
                        breadCrumbActive="Betting Inspection"
                    />
                    <Row>
                        <Col sm="12">
                            <Card>
                                <CardHeader>
                                    <CardTitle>Betting Inspection</CardTitle>
                                </CardHeader>
                                <CardBody className="rdt_Wrapper">


                                    <SystemStatusTypeSearch
                                        handleSearchSystemStatusType={this.handleCloseStatusSearch}
                                        opensearch={this.state.openstatussearch}
                                        handleHideSearch={this.handleHideStatusSearch}
                                        value={searchstatusvalue}
                                        data={searchstatusdata}
                                        handleFilter={this.handleSearchStatusFilter}
                                        filteredData={filteredStatusData}
                                    />
                                    <BusinessPopover
                                        opensearch={this.state.openbusinesssearchEye}
                                        handleHideSearch={this.handleHideBusinessSearchEye}
                                        BusinessNamePopup={this.state.BUSINESS_NAME}
                                        changeBusinessNamePopup={e => this.setState({ BUSINESS_NAME: e.target.value })}
                                        BusinessNo={this.state.REG_BUSINESS_ID}
                                        changeBusinessNo={e => this.setState({ REG_BUSINESS_ID: e.target.value })}
                                        BusinessType={this.state.BUSINESS_TYPE_NAME}
                                        changeBusinessType={e => this.setState({ BUSINESS_TYPE_NAME: e.target.value })}
                                        Subcounty={this.state.SUBCOUNTY_NAME}
                                        changeSubcounty={e => this.setState({ SUBCOUNTY_NAME: e.target.value })}
                                    />
                                    <ClientPopover
                                        opensearch={this.state.openclientsearchEye}
                                        handleHideSearch={this.handleHideClientSearchEye}
                                        ClientNamePopUp={this.state.CLIENT_NAME}
                                        handleChangeClientNamePopUp={e => this.setState({ CLIENT_NAME: e.target.value })}
                                        ClientDocumentNumber={this.state.CLIENT_DOCUMENT_NUMBER}
                                        changeClientDocumentNumber={e => this.setState({ CLIENT_DOCUMENT_NUMBER: e.target.value })}
                                        ClientPostalAddress={this.state.CLIENT_POSTAL_ADDRESS}
                                        changeClientPostalAddress={e => this.setState({ CLIENT_POSTAL_ADDRESS: e.target.value })}
                                        ClientMobileNumber={this.state.CLIENT_MOBILE_NUMBER}
                                        changeClientMobileNumber={e => this.setState({ CLIENT_MOBILE_NUMBER: e.target.value })}
                                    />
                                    <BettingInspectionTable
                                        role="BettingInspection"
                                        handleEdit={this.handleEditData}
                                        handleDelete={this.handleDeleteData}
                                        value={value}
                                        data={data}
                                        columns={columns}
                                        filteredData={filteredData}
                                        handleShow={this.handleShow}
                                        handleFilter={this.handleFilter}
                                    />

                                    <BettingInspectionModal
                                        validated={this.state.validated}
                                        show={this.state.show}
                                        handleHide={this.handleHide}
                                        InsertUpdate={this.state.InsertUpdate}
                                        handleSubmit={this.handleSubmit}
                                        handleShowBusinessSearch={this.handleShowBusinessSearch}
                                        handleShowBusinessSearchEye={this.handleShowBusinessSearchEye}
                                        handleShowClientSearchEye={this.handleShowClientSearchEye}
                                        handleShowStatusSearch={this.handleShowStatusSearch}
                                        handleShowClientRegistrationSearch={this.handleShowClientRegistrationSearch}
                                        BusinessName={this.state.BUSINESS_NAME}
                                        changeBusinessName={e => this.setState({ BUSINESS_NAME: e.target.value })}

                                        InspectionDetails={this.state.INSPECTION_DETAILS}
                                        changeInspectionDetails={e => this.setState({ INSPECTION_DETAILS: e.target.value })}

                                        SystemStatusTypeDescription={this.state.SYSTEMSTATUSTYPEDESCRIPTION}





                                        showdelete={this.state.showdelete}
                                        handleHideDelete={this.handleHideDelete}
                                        handleDelete={this.handleDelete}

                                        BusinessNo={this.state.REG_BUSINESS_ID}
                                        changeBusinessNo={e => this.setState({ REG_BUSINESS_ID: e.target.value })}
                                        BusinessType={this.state.BUSINESS_TYPE_NAME}
                                        changeBusinessType={e => this.setState({ BUSINESS_TYPE_NAME: e.target.value })}
                                        BusinessDocType={this.state.DOCUMENT_TYPE_NAME}
                                        changeBusinessDocType={e => this.setState({ DOCUMENT_TYPE_NAME: e.target.value })}
                                        ClientName={this.state.CLIENT_NAME}
                                        handleChangeClientName={e => this.setState({ CLIENT_NAME: e.target.value })}
                                        ClientDocumentNumber={this.state.CLIENT_DOCUMENT_NUMBER}
                                        changeClientDocumentNumber={e => this.setState({ CLIENT_DOCUMENT_NUMBER: e.target.value })}
                                        ClientPostalAddress={this.state.CLIENT_POSTAL_ADDRESS}
                                        changeClientPostalAddress={e => this.setState({ CLIENT_POSTAL_ADDRESS: e.target.value })}
                                        ClientMobileNumber={this.state.CLIENT_MOBILE_NUMBER}
                                        changeClientMobileNumber={e => this.setState({ CLIENT_MOBILE_NUMBER: e.target.value })}
                                        BettingRegistrationNumber={this.state.BETTING_NUMBER}
                                        changeBettingRegistrationNumber={e => this.setState({ BETTING_NUMBER: e.target.value })}
                                    />


                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </React.Fragment>

            </>

        )
    }
}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default (connect(mapStateToProps)(BettingInspection));